.footer_outer_container {
	background-color: #111;
	color: grey;
    display: flex;
    justify-content: center;
    margin-top: 2%;
}
.footer_inner_container{
    width: 70%;
    height: 100%;
    /* background-color: red; */
}
.footer_icons{
    /* background-color: blue; */
    width: 15%;
    display: flex;
    justify-content: space-between;
    margin-left: 10px;
    color: #fff;
}
.footer_data{
    display: flex;
    justify-content: space-between;
}
.footer_data ul{
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 18vh;
    /* background-color: blue; */
}
.footer_data ul li{
    cursor: pointer;
}
.service_code {
    display: flex;
    align-items: center;
    justify-content: center;
    border:#fff 1px solid;
    width:15%;
    margin:10px 0;
    cursor: pointer;
}
.copy-write{
    font-size: 12px;
    margin-bottom:10px;
}

@media (max-width: 768px) {
    .footer_outer_container{

        .footer_icons{
            width: 40%;
        }

        

      .footer_data, .service_code, .copy-write {
            width: 100%;
            margin-bottom: 20px; /* Add spacing between divs */
          }
    }

    .footer_data {
        display: flex;
        flex-wrap: wrap; /* Allow items to wrap to the next line */
        justify-content: space-between;
      }
      
      .footer_data > div {
        width: calc(25% - 20px); /* Adjust width and margin as needed */
        box-sizing: border-box;
      }
      

    

    
}

@media (max-width: 450px){
    .footer_outer_container {
        .footer_data, .service_code, .copy-write {
           
            display: flex;
            flex-direction: column;
            margin-top: 30px;
        }
    }
}

